import React, { Component } from "react";
import AudioVisual from "./AudioVisual";

class AudioAnalyser extends Component {
  constructor(props) {
    super(props);
    this.state = { audiodata: new Uint8Array(0) };
    this._tick = this.tick.bind(this);
    this._mindb = 240;
    this._level = 0;
  }
  componentDidMount() {
    this.getMax();
    this.audiocontext = new (window.AudioContext ||
      window.webkitAudioContext())();
    this.analyser = this.audiocontext.createAnalyser();
    this.dataArray = new Uint8Array(this.analyser.frequencyBinCount);
    this.source = this.audiocontext.createMediaStreamSource(this.props.audio);

    this.source.connect(this.analyser);
    this.rafId = requestAnimationFrame(this._tick);
  }
  componentWillUnmount() {
    cancelAnimationFrame(this.rafId);
    this.analyser.disconnect();
    this.source.disconnect();
  }
  getMax() {
    Uint8Array.prototype.max = function () {
      return Math.max.apply(null, this);
    };
    Uint8Array.prototype.min = function () {
      return Math.min.apply(null, this);
    };
  }
  tick() {
    this.analyser.getByteTimeDomainData(this.dataArray);
    const maxvalue = this.dataArray.max();
    this.setState({ audiodata: this.dataArray });
    this.initAnimation(maxvalue);
    this.rafId = requestAnimationFrame(this._tick);
  }
  initAnimation(db) {
    var cnt = document.getElementById("count");
    var water = document.getElementById("water");
    var persen = parseFloat(cnt.innerText);
    var interval;
    var add = db > this._mindb ? 1 : -1 ;

    if (persen >= 0 && persen < 100) {
      console.log(db + " db");
      persen = persen + add;
      console.log(persen);
      if(persen<0){
        persen = 0;
      }
      cnt.innerText = persen;
      water.style.transform = "translate(0" + "," + (100 - persen) + "%)";
    }
  }
  render() {
    return (
      <div>
        {/* <AudioVisual audiodata={this.state.audiodata} /> */}
        {
          // Animation Water fill
        }
        <svg viewBox="0 0 0 0" style={{ display: "none" }}>
          <symbol id="wave">
            <path d="M420,20c21.5-0.4,38.8-2.5,51.1-4.5c13.4-2.2,26.5-5.2,27.3-5.4C514,6.5,518,4.7,528.5,2.7c7.1-1.3,17.9-2.8,31.5-2.7c0,0,0,0,0,0v20H420z"></path>
            <path d="M420,20c-21.5-0.4-38.8-2.5-51.1-4.5c-13.4-2.2-26.5-5.2-27.3-5.4C326,6.5,322,4.7,311.5,2.7C304.3,1.4,293.6-0.1,280,0c0,0,0,0,0,0v20H420z"></path>
            <path d="M140,20c21.5-0.4,38.8-2.5,51.1-4.5c13.4-2.2,26.5-5.2,27.3-5.4C234,6.5,238,4.7,248.5,2.7c7.1-1.3,17.9-2.8,31.5-2.7c0,0,0,0,0,0v20H140z"></path>
            <path d="M140,20c-21.5-0.4-38.8-2.5-51.1-4.5c-13.4-2.2-26.5-5.2-27.3-5.4C46,6.5,42,4.7,31.5,2.7C24.3,1.4,13.6-0.1,0,0c0,0,0,0,0,0l0,20H140z"></path>
          </symbol>
        </svg>
        <div className="box">
          <div className="percent">
            <div className="percentNum" id="count">
              0
            </div>
            <div className="percentB">%</div>
          </div>
          <div id="water" className="water">
            <svg viewBox="0 0 560 20" className="water_wave water_wave_back">
              <use xlinkHref="#wave"></use>
            </svg>
            <svg viewBox="0 0 560 20" className="water_wave water_wave_front">
              <use xlinkHref="#wave"></use>
            </svg>
          </div>
        </div>
      </div>
    );
  }
}

export default AudioAnalyser;
