import "./App.css";
import "./Styles.scss";
import DecibelMeter from "decibel-meter";
import React from "react";
import AudioAnalyser from "./AudioAnalisy";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      permission: false,
      audio: null,
      db: {
        db: 0,
        percen: 0,
        value: 0,
      },
    };
  }
  componentDidMount() {
    //this.initAnimation();
  }
  initmeter() {
    const meter = new DecibelMeter("meter-1");
    meter.sources.then((sources) => console.log(sources));
    // first method

    // meter.sources.then((sources) => {
    //   console.log(sources[0])
    //   meter.connect(sources[0]);
    // });

    // second method

    meter.connectTo("default"); // find audioinput by deviceId, and connect if possible
    meter.connectTo(0); // find the first audioinput, and connect if possible

    // handling errors

    //meter.connectTo("not-real").catch((err) => alert("Connection Error"));
    meter.on("connect", (source, previous) => {
      console.log(`
          Connected to ${source?.label}
          Disconnected from ${previous?.label}`);
    });
    meter.on("sample", (db, percen, value) => {
      //console.log(`${db} dB : ${percen} % : ${value} value`);
      let decible = {
        db,
        percen,
        value,
      };
      this.setState({
        db: decible,
      });
    });
    meter.listen();
  }
  async checkPermission() {
    try {
      const audio = await navigator.mediaDevices.getUserMedia({
        audio: true,
        video: false,
      });
      console.log("audio ", audio);
      this.setState({
        permission: !this.state.permission,
        audio,
      });
    } catch (error) {
      console.log("error ", error);
    }
  }
  initAnimation() {
    var cnt = document.getElementById("count");
    var water = document.getElementById("water");
    var persen = cnt.innerText;
    var interval;

    interval = setInterval(() => {
      persen++;
      cnt.innerText = persen;
      water.style.transform = 'translate(0'+','+(100-persen)+'%)'
      if(persen === 100){
        clearInterval(interval);
      }
    }, 120);
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
          {this.state.audio === null && (
            <div>
              <button onClick={this.checkPermission.bind(this)}>
                Play
              </button>
            </div>
          )}

          {this.state.audio ? <AudioAnalyser audio={this.state.audio} /> : ""}
          
        </header>
      </div>
    );
  }
}

export default App;
